  fill: #ebeff3;
}

.svg_2 path {
  fill: #fff;
}

.svg_container {
  --divider-height: 4rem;
  position: relative;
  top: calc( var(--divider-height)/2 * -1);
  height: var(--divider-height);
  width: 100%;
  /* alternatively, could get rid of the rect, and move the svg container further down */
  float: left;
  z-index: 1;
}

/* NAVBAR */
.navbar {
  justify-content: initial;
}

.navbar__container {
  flex: 1 0 10%;
}

.navbar .sub-nav {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  flex: 1 0 65%;
}

.navbar .sub-nav .navbar-nav, .navbar .sub-nav .nav-link {
  font-family: "Source Sans Pro";
  font-size: 16px;
  color: #1c1c1c;
  text-decoration: none;
  padding: 5px 15px;
}

.navbar .sub-nav .navbar-nav {
  flex-direction: row;
  margin-top: 0;
  align-items: center;
}

.navbar .sub-nav .navbar-nav .nav-item:last-child {
  margin-right: 0;
}

.navbar .sub-nav .navbar-nav .nav-item.register__button {
  align-items: center;
  display: flex;
  margin-left: 10px;
}

.navbar .sub-nav .navbar-nav .nav-item.register__button a {
  flex: 1;
}

.navbar .sub-nav .nav-link.active {
  background-color: #ebeff3;
  color: #1c1c1c;
  padding: 5px 15px;
  border-radius: 3px;
}

.numlist > * {
  flex-basis: 0;
  flex-grow: 1;
  padding: .5rem;
}

.number {
  font-size: 5rem;
  font-weight: bold;
  color: #53657d;
  font-family: 'Evolventa';
}

/* PANE */
.leftpane {
  background-image: linear-gradient(to left, #0053b3, #003b80);
  color: #fff;
}

.leftpane a {
  color: #fff;
  color: var(--white);
}

.leftpane a:hover {
  background: #53657d;
  background: var(--darker-grey);
}

.pane {
  padding: 3em;
  padding: var(--space-xl);
  margin: 0;
}

footer.footer {
  padding: 50px 0 80px 0;
}

footer.footer label {
  color: white;
  font-size: 16px;
}

footer.footer .footer-link li {
  margin-bottom: 10px;
  padding: 0;
}

footer.footer .footer-link li > a {
  color: white;
  text-decoration: none;
}

footer.footer .social-link {
  display: flex;
  justify-content: space-between;
  margin: 25px 0;
}

footer.footer .social-link li {
  margin-bottom: 0;
  display: flex;
  align-items: center;
  font-size: 30px;
}

footer.footer .social-link svg {
  height: 30px;
}

footer.footer .social-link svg path {
  fill: white;
}

@media (max-width: 850px) {
  .text_1 {
    max-width: 50em;
  }
  .text_2 {
    max-width: 50em;
  }
  form input[type="submit"] {
    white-space: normal;
  }
  a.btn-xl {
    width: 100%;
  }
  .row > * + * {
    margin-left: 0;
    margin-top: 2.5em;
  }
  .button-submit__footer {
    width: 100%;
  }
}

@media screen and (max-width: 790px) {
  .sub-nav {
    flex-direction: column;
  }
  .navbar-nav:first-child {
    margin: 10px 0 20px 0;
    margin-left: 0 !important;
    flex-wrap: wrap;
    text-align: center;
  }
  .nav-item {
    flex: 1 0 30%;
  }
  .nav-item.register__button {
    flex-basis: 100%;
    display: flex;
    text-align: center;
    margin-top: 20px;
    margin-left: 0;
  }
}

@media (max-width: 749px) {
  .illu-1 {
    width: 100%;
    top: 0;
  }
}

@media screen and (max-width: 1000px) {
  .illu-1 {
    position: relative;
    margin-top: 15px;
    width: auto;
  }
}
